<template>
  <section class="toy-app">
    <router-link to="/user/edit">Edit Profile</router-link>
    <filter-by @update-filter="updateFilter" :toys="getToys" />
    <router-link to="/toy/edit">
      <v-btn fab dark color="indigo" class="add-btn">
        <v-icon dark>add</v-icon>
      </v-btn>
    </router-link>
    <router-link to="/toy/dashboard">Dashboard</router-link>
    <toy-list :toys="getToys" @remove-toy="removeToy"></toy-list>
  </section>
</template>

<script>
import toyList from "@/components/toyList";
import filterBy from "@/components/filterBy";

export default {
  created() {
    this.$store.dispatch({ type: "loadToys" });
  },
  data() {
    return {};
  },
  methods: {
    updateFilter(filterBy) {
      // this.$store.commit({ type: 'updateFilter', filterBy})
      this.$store.dispatch({ type: "getFilteredToys", filterBy });
    },
    removeToy(toyId){
      this.$store.dispatch({type:'removeToy', toyId})
    }
  },
  computed: {
    getToys() {
      return this.$store.getters.toysToShow;
    }
  },
  components: {
    toyList,
    filterBy
  }
};
</script>

