import axios from 'axios'

export default {
    login,
}

async function login(user){
    const res = await axios.post(_getUrl('/login/'), user)
    return res.data
}


function _getUrl(params = '') {
    const BASE_URL = (process.env.NODE_ENV !== 'development') ? '/api/user' : 'http://localhost:3000/api/user';
    return `${BASE_URL}/${params}`
}
