<template>
  <section class="time-picker">
    Pick Time:
    <input type="time" :value="value" @input="updateTime">
  </section>
</template>


<script>

export default {
  props:['value'],
  data(){
    return{
      time:this.value,
    }
  },
  methods:{
    updateTime(ev){
      this.time = ev.target.value
      this.$emit('input', this.time)
    }
  }
}
</script>



