import userService from "@/services/userService";

export default {
  created() {},
  data() {
    return {
      user: {
        nickname: "",
        password: ""
      }
    };
  },
  methods: {
    async doLogin() {
      try {
        const isUser = await userService.login(this.user)
        this.$router.push('/toy')
      } catch(err){
        console.log("got error:", err);
      }
    }
  },
};
</script>

