import Vue from 'vue'
import Vuex from 'vuex'
import toyStore from './modules/toyStore'



Vue.use(Vuex)

export default new Vuex.Store({
  strict: true,
  state: {
    isChatOpen: false,
  },
  getters:{
    isChatOpen(state){
      return state.isChatOpen
    }
  },
  mutations: {
    toggleChat(state){
      state.isChatOpen = !state.isChatOpen
    }
  },
  actions: {
    sendMsg(){
      console.log('sending')
    }
  },
  modules:{
    toyStore
  }
})
