<template>
  <section class="dashboard-containe">
    <router-link to="/">BACK</router-link>
    <Chart class="chart" v-if="pricesPerToyChart.label" :chartData="pricesPerToyChart"/>
  </section>
</template>


<script>
import Chart from '../components/Chart';
import toyService from '@/services/toyService'

export default {
    data(){
        return{
            pricesPerToyChart:{labels:[],data:[], label:''},
        }
    },
    created(){
        toyService.getPricesPerToy()
          .then(avgPricePerType => {
            avgPricePerType.forEach(typeInfo => {
              this.pricesPerToyChart.labels.push(typeInfo.type)
              this.pricesPerToyChart.data.push(typeInfo.avgPrice)
            })
            this.pricesPerToyChart.label = 'Avg Price'
          })
    },
    components: {
        Chart
    }
};
</script>


<style lang="scss" scoped>
.chart {
  display: block;
  max-height: 500px;
  max-width: 500px;
}
</style>

