<template>
  <section class="toy-list" v-if="toys">
    <ul class="flex">
        <toy-preview v-for="currToy in toys" :key="currToy._id" :toy="currToy"
                    @emit-remove="emitRemove">
        </toy-preview>
    </ul>
  </section>
</template>


<script>
import toyPreview from "./toyPreview";

export default {
  props: {
    toys: {
      required: true,
      type: Array,
    }
  },
  components: {
      toyPreview
  },
  methods:{
    emitRemove(toyId){
      this.$emit('remove-toy', toyId)
    }
  }
};
</script>


