<template>
  <section class="gender-picker">
    Pick Gender:
    <select ref="gender" :value="value" @input="updateGender">
      {{value}}
      <option value="male">Male</option>
      <option value="female">Female</option>
      <option value="notSay">Rather Not Say</option>
    </select>
  </section>
</template>


<script>

export default {
  props:['value'],
  data(){
    return{
      userGender:this.value,
    }
  },
  methods:{
    updateGender(ev){
      this.userGender = ev.target.value
      this.$emit('input', this.userGender)
    }
  }
}
</script>



