<template>
  <section v-if="isChatOpen" class="support-chat">
      <header>
          <button @click="toggleChat">X</button>
      </header>
      <main>
        IM A CHAT
      </main>
      <footer>
          <form @submit.prevent="sendMsg">
            <input type="text" v-model="msg">
            <button type="submit">
                <img src="@/assets/send-icon.png" class="send-icon">
            </button>
          </form>
      </footer>
  </section>
</template>

<script>
export default {
    data(){
        return{
            msg:'',
        }
    },
    computed:{
        isChatOpen(){
            return this.$store.getters.isChatOpen
        }
    },
    methods: {
        toggleChat(){
            this.$store.commit({type:'toggleChat'})
        },
        sendMsg(){
            this.$store.dispatch({type:'sendMsg', msg: this.msg})
        }
    },
}
</script>

<style lang="scss" scoped src="@/styles/components/supportChat.scss">
</style>

