import toyService from '../services/toyService'

export default {
    state: {
        toys: [],
        isLoading: false,
        filterBy: {
            name: '',
            stockStatus: '',
            type: '',
        },
    },
    getters: {
        toysToShow(state) {
            return state.toys
            // let toys = state.toys
            // let { name, stockStatus, type } = state.filterBy
            // toys = toys.filter(currToy => currToy.name.includes(name))
            // if (stockStatus === 'inStock') toys = toys.filter(toy => toy.inStock);
            // if (type !== 'all') toys = toys.filter(toy => toy.type === type);
            // return toys
        },
    },
    mutations: {
        setToys(state, { toys }) {
            state.toys = toys
        },
        isLoading(state, { isLoading }) {
            state.isLoading = isLoading;
        },
        updateFilter(state, { filterBy }) {
            state.filterBy = filterBy
        },
        deleteToy(state, { toyId }) {
            let toyIdx = state.toys.findIndex(toy => {
                return toy._id === toyId
            })
            state.toys.splice(toyIdx, 1);
        }
    },
    actions: {
        loadToys(context) {
            // context.commit({ type: 'isLoading', isLoading: true })
            return toyService.query()
                .then(toys => {
                    // context.commit({ type: 'isLoading', isLoading: false })
                    context.commit({ type: 'setToys', toys: toys.reverse() })
                })
        },
        toyById(context, { toyId }) {
            return toyService.getById(toyId)
        },
        updateToy(context, { toy }) {
            return toyService.update(toy)
        },
        getFilteredToys(context, { filterBy }) {
            toyService.getToysByFilter(filterBy)
                .then(toys => context.commit({ type: 'setToys', toys }))
        },
        removeToy(context, { toyId }) {
            toyService.remove(toyId)
                .then(() => context.commit({ type: 'deleteToy', toyId }))
        }
    }
}