<script>
import { Bar } from 'vue-chartjs'

export default {
    props:['chartData'],
    extends: Bar,
    mounted () {
        // Overwriting base render method with actual data.
        this.renderChart({
        labels: this.chartData.labels,
        datasets: [
            {
            label: this.chartData.label,
            backgroundColor: '#f87979',
            data: this.chartData.data
            }
        ]
        })
    }
}
</script>
