<template>
  <section class="toy-details" v-if="toy">
    <h1>Toy Details</h1>
    <pre>{{toy}}</pre>
    Created
    <span>{{ [toy.createdAt, 'milliseconds'] | duration('subtract', Date.now()) | duration('humanize', true) }}</span>
    <router-link to="/">Back</router-link>
  </section>
</template>

<script>
export default {
  data() {
    return {
      toy: null
    };
  },
  created() {
    const toyId = this.$route.params.theToyId;
      this.$store.dispatch({ type: "toyById", toyId }).then(foundToy => {
        this.toy = foundToy;
      });
  }
};
</script>
