<template>
  <section class="filter-by">
    <form @submit.prevent="emitFilter" class="filter-form">
      <v-flex xs12 sm6 md3 class="search">
        <v-text-field label="Search" placeholder="Enter Toy Name..."
                      v-model="filterBy.name">
        </v-text-field>
      </v-flex>
      <div class="stock-status">
        <v-switch v-model="filterBy.stockStatus" :label="getSwitchLabel"></v-switch>
      </div>
      <div class="type">
        <v-flex xs12 sm6 d-flex>
          <v-select :items="getToyTypes" label="Toy Type:" v-model="filterBy.type"></v-select>
        </v-flex>
      </div>
      <v-btn type="submit" fab dark small color="primary" class="search-btn">
        <v-icon>search</v-icon>
      </v-btn>
    </form>
  </section>
</template>


<script>
export default {
  data() {
    return {
      filterBy: {
        name: "",
        stockStatus: "",
        type: ""
      }
    };
  },
  props: ["toys"],
  methods: {
    emitFilter() {
      const filterByCopy = JSON.parse(JSON.stringify(this.filterBy));
      this.$emit("update-filter", filterByCopy);
    }
  },
  computed: {
    getToyTypes() {
      let types = this.toys.map(toy => toy.type);
      types.unshift("");
      return types
    },
    getSwitchLabel(){
      let str = 'Show'
      if(this.filterBy.stockStatus) return str += ' In Stock'
      else return str += ' All'
    }
  }
};
</script>



<style lang="scss" scoped>
input {
  border: 1px solid grey;
}

.filter-form {
  & {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
  }

  .search {
    max-width: 135px;
  }
}

.search-btn {
  height: 30px;
  width: 30px;
}
</style>




