<template>
  <section class="toy-edit" v-if="toyCopy">
    <div class="edit-container">
      <h1>{{toyCopy._id ? 'Edit Toy' : 'Add Toy'}}</h1>
      <form @submit.prevent="saveChanges">
        Name:
        <input placeholder="Enter Name..." type="text" v-model="toyCopy.name" />
        Enter Price: 
        <input type="number" v-model="toyCopy.price" />
        <input placeholder="Enter Type..." type="text" v-model="toyCopy.type" />
        In Stock? 
        Yes:
        <input type="radio" name="stock-status" :value="true" v-model="toyCopy.inStock"/> 
        No:
        <input type="radio" name="stock-status" :value="false" v-model="toyCopy.inStock"/>
        <input type="file" @change="uploadImg">
        <button type="submit">{{toyCopy._id ? 'SAVE' : 'ADD'}}</button>
      </form>
    </div>
    <router-link to="/">Back To Toys</router-link>
  </section>
</template>


<script>
import toyService from '../services/toyService'
export default {
  data() {
    return {
        toyCopy: {
            name: '',
            price: 0,
            type: '',
            createdAt: Date.now(),
            inStock: true
        }
    }
  },
  created() {
    const toyId = this.$route.params.theToyId;
    if (toyId) {
      this.$store.dispatch({ type: "toyById", toyId }).then(foundToy => {
        this.toyCopy = JSON.parse(JSON.stringify(foundToy));
      });
    }
  },
  methods: {
    saveChanges() {
        this.toyCopy.price = parseInt(this.toyCopy.price)
        this.$store.dispatch({ type: "updateToy", toy: this.toyCopy })
            .then(() => {
                this.$router.push("/");
            })
            .catch(err => console.log("had problems reporting to server"));
    },
    uploadImg(ev){
      toyService.handleUploadImg(ev)
    }
  }
};
</script>


<style scoped>
  button{
    border:1px solid grey;
  }
</style>
