import axios from 'axios'

export default {
    query,
    getById,
    update,
    getToysByFilter,
    getPricesPerToy,
    remove,
    handleUploadImg
}

function query() {
    return axios.get(_getUrl())
        .then(res => res.data)
}

function handleUploadImg(ev) {
    console.log(ev.target.files[0])
    const form = new FormData()
    form.append('imgUpload', ev.target.files[0])
    axios.post('http://localhost:3000/upload/cloudinary', form)
        .then(res => res.json())
        .then(res => console.log(res.url))
}

function update(toy) {
    if (toy._id) {
        return axios.put(_getUrl(toy._id), toy)
            .then(res => res.data)
    } else {
        return axios.post(_getUrl(), toy)
            .then(res => res.data)
    }
}

function remove(id) {
    return axios.delete(_getUrl(id))
        .then(res => res.data)
}

function getToysByFilter(filterBy, sortBy = {}) {
    return axios.get(_getUrl(), { params: filterBy })
        .then(res => res.data)
}

function getById(id) {
    return axios.get(_getUrl(id))
        .then(res => res.data)
}

function _getUrl(id = '') {
    const BASE_URL = (process.env.NODE_ENV !== 'development') ? '/api/toy' : 'http://localhost:3000/api/toy';
    return `${BASE_URL}/${id}`
}

function getPricesPerToy() {
    let pricesPerToy = {}
    return query()
        .then(toys => {
            toys.forEach(toy => {
                if (pricesPerToy[toy.type]) {
                    pricesPerToy[toy.type].type = toy.type
                    pricesPerToy[toy.type].totalPrice += +toy.price
                    pricesPerToy[toy.type].totalLength += 1
                } else pricesPerToy[toy.type] = { totalPrice: +toy.price, totalLength: 1 }
            })
            pricesPerToy = Object.values(pricesPerToy);
            return pricesPerToy.map(typeInfo => {
                return {
                    type: typeInfo.type,
                    avgPrice: typeInfo.totalPrice / typeInfo.totalLength
                }
            })
        })
}